export default {
  state: {
    UploadingItems: [],
    RewriteItemsData: null
  },
  mutations: {
    addUploadingItem(state, item) {
      state.UploadingItems.push(item);
    },

    removeUploadingItem(state, value) {
      state.UploadingItems = state.UploadingItems.filter(
        item => item.UploadItem === value
      );
    },

    setRewriteItemsData(state, data) {
      state.RewriteItemsData = data;
    }
  }
};
